import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			is_bind: '',
			orderId: '',
			placeRecharge: '',
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		order() {
			this.leftClick()
		},
		/**
		 * 充值信息
		 */
		placeRechargea() {
			var that = this
			var id = that.orderId;









			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				id: id,
				type: type,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				id: id,
				type: type,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("placeRecharge", data, (res) => {
				if (res.data.code == 200) {
					that.placeRecharge = res.data.data
				} else if (res.data.code == 406) {
					rs.Toast('请先绑定微信')
				} else {
					rs.Toast(res.data.msg)
				}
			})
		},






































































































	},
	onLoad(options) {
		this.orderId = options.orderId
	},
	onShow() {
		var that = this;
		that.placeRechargea()
	}
};
